@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-dark: #282c34;
  --bg-light: #e4e4e4;
  --bg-faded: #383c48;
  --primary: #f9cc46;
  --faded: #939aab;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}


.home-wrapper {
	background-color: var(--bg-dark);
	min-height: 100vh;
	padding: 2rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.cat {
	width: 100px;
	height: 100px;
	border-radius: 50%;
	object-fit: cover;
	margin-bottom: 1rem;
}

.title {
	color: var(--primary);
	font-size: 1.5rem;
	margin-bottom: 1rem;
}

#search-form {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.search {
	font-size: 1.1rem;
	padding: 10px;
	width: 80%;
	background-color: var(--bg-faded);
	border: none;
	outline: none;
	border-radius: 10px;
	margin-bottom: 15px;
	color: var(--primary);
	font-family: inherit;
}

.search-btn {
	border: none;
	border-radius: 10px;
	padding: 10px;
	width: 80%;
	background-color: var(--primary);
	font-family: inherit;
	font-weight: 700;
	font-size: 1rem;
	color: var(--bg-dark);
	cursor: pointer;
	transition: all 0.3s ease;
}

.search-btn:hover {
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
	opacity: 0.9;
}

.spinner {
	color: var(--bg-dark);
}

.error-msg {
	font-size: 13px;
	margin-top: 10px;
	color: #ff6060;
}
.user-wrapper {
	min-height: 100vh;
	-webkit-animation: scale 0.3s forwards;
	        animation: scale 0.3s forwards;
}

@-webkit-keyframes scale {
	from {
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
		opacity: 0.5;
	}
	to {
		-webkit-transform: scale(1.0);
		        transform: scale(1.0);
		opacity: 1;
	}
}

@keyframes scale {
	from {
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
		opacity: 0.5;
	}
	to {
		-webkit-transform: scale(1.0);
		        transform: scale(1.0);
		opacity: 1;
	}
}

.banner {
	min-height: 40vh;
	background-color: var(--bg-dark);
	padding: 1rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.banner-inner {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 30px;
	align-items: center;
	justify-items: center;
	max-width: 900px;
	margin: 0 auto;
}

.profile-image {
	width: 150px;
	height: 150px;
	border-radius: 50%;
	object-fit: cover;
}

.user-info {
	text-align: left;
}

.user-name {
	color: var(--primary);
	margin-bottom: 5px;
}

.user-login {
	color: var(--bg-light);
	margin-bottom: 10px;
}

.user-joined {
	font-size: 15px;
	color: var(--faded);
	margin-bottom: 5px;
}

.user-contact {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 10px;
}

.user-contact p{
	font-size: 12px;
	margin-right: 20px;
	color: var(--faded);
}

.contact-icon {
	margin-right: 5px;
}

.user-bio {
	margin-bottom: 10px;
	color: var(--bg-light);
}

.github-link {
	background-color: var(--primary);
	color: var(--bg-dark);
	padding: 8px 10px;
	border-radius: 10px;
	text-decoration: none;
	font-size: 0.8rem;
	transition: all 0.3s ease-in-out;
}

.github-link:hover {
	opacity: 0.8;
	-webkit-transform: translateY(-3px);
	        transform: translateY(-3px);
}

.body {
	background-color: var(--bg-light);
	min-height: 60vh;
	padding: 1rem;	
}

.body-inner {
	max-width: 1100px;
	margin: 0 auto;
}

.detail-tablets {
	max-width: 600px;
	margin: 20px auto;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.detail-tab {
	margin: 5px;
}

.repos-title {
	margin-bottom: 15px;
	color: var(--bg-dark);
}

.repos-container {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 20px;
}

@media(max-width: 500px) {

	.banner {
		padding: 2rem 1rem;
	}

	.banner-inner {
		grid-template-columns: 1fr;
	}

	.profile-image {
		width: 120px;
		height: 120px;
	}

	.user-info {
		text-align: center;
	}

	.repos-container {
		grid-template-columns: 1fr;
	}

}


.repo {
	background-color: #f1f1f1;
	padding: 1rem;
	margin-bottom: 10px;
	border-radius: 10px;
	color: var(--bg-dark);
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
}

.repo-top {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.repo-name {
	font-family: 'Fira Code', monospace;
	margin-bottom: 10px;
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.repo-name h2 {
	font-size: 1.2rem;
}

.github-repo-link {
	display: inline-block;
	font-size: 1rem;
	color: var(--bg-dark);
}

.github-repo-link:hover {
	color: var(--primary);
}

.repo-icon {
	margin-right: 10px;
}

.desc {
	font-size: 0.8rem;
	font-weight: 300;
	margin-bottom: 10px;
	text-align: left;
}

.repo-details {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.repo-details span {
	margin-right: 15px;
	font-size: 0.6rem;
}

.repo-language {
	background-color: var(--primary);
	color: var(--bg-dark);
	border-radius: 5px;
	padding: 5px 8px;
}

.repo-size {
	font-weight: 300;
}


@media(max-width: 500px) {

	.repo {
		padding: 0.8rem;
	}

}
